.tabs{

    width: 100vw;
    display:flex;
    align-content: stretch;
    justify-content: space-around;
}
.tab {
    cursor: pointer;
    height: 120px;
    padding: 10px 15px;
    transition: 0.3s;
    margin: 0;
    color: white;
    background-color: #996633;
    display:flex;
    flex-grow: 1;
    box-shadow: 0 1px 5px rgb(0 0 0 / 0.2);
    border-top: 1px solid #000000;
    border-bottom: 1px dashed #000000;
    border-left: 0;
    border-right:1px solid grey;
    display: flex;
    flex-direction: column;
    justify-content: center;

}
.tab__subtitle{
    text-align: left;
    font-size: 1.5rem;
    font-weight: lighter;
}
.tab__title{
    font-weight: 600;
    margin-bottom: 2px;
    font-size: 2rem;
}

.tab:first-child {
    border-left: 1px solid #000000;
}

.tab:nth-child(2n){
   background-color: cornflowerblue;
   color:white;
}
.tab:hover {
    background-color: #ddd;
}

.tabcontent {
    display: none;
    padding: 15px;
    border-top: none;
}